<template>
  <div id="app">
    <!-- <app-header v-bind:variable="poiInfo"></app-header> -->
    <app-header v-bind:poiInfo="poiInfo"></app-header>
    <app-nav :commentNum="commentNum"></app-nav>
    <!-- 保留组件状态 -->
    <!-- <keep-alive> -->
      <router-view></router-view>
    <!-- </keep-alive> -->

  </div>
</template>

<script>
import Header from "./components/header/Header";
import Nav from "./components/nav/Nav";
export default {
  name: "App",
  data: function() {
    return {
      poiInfo: {},
      commentNum: 0
    };
  },
  components: {
    "app-header": Header,
    "app-nav": Nav
  },
  created() {
    fetch("/api/goods")
      .then(res => {
        return res.json();
        /* 用fetch请求的数据是可读的数据流需return res.json()转化 */
      })
      .then(response => {
        console.log(response);
        if (response.code == 0) {
          // this.poiInfo = response.data.poi_info.name
          this.poiInfo = response.data.poi_info;
          // 请求的是poi_info下的大括号对象
        }
      });
    // 请求ratings
    fetch("/api/ratings")
      .then(res => {
        return res.json();
      })
      .then(response => {
        if (response.code == 0) {
          this.commentNum = response.data.comment_num;
          // 请求的是comment_num属性值 即评价(3683)导航
          /* 只有在App.vue组件中请求的数据，传给nav.vue */
        }
      });
  }
};
</script>

<style>
</style>



