// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VueRouter from "vue-router";
import Goods from "./components/goods/Goods";
import Ratings from "./components/ratings/Ratings";
import Seller from "./components/seller/Seller";
Vue.config.productionTip = false
Vue.use(VueRouter); //必须要通过 Vue.use() 明确地安装路由功能
    // routes选项配置路由表
  const routes=[{
    path:"/",
    component:Goods
  }, //多加一个路由表，则表示页面最开始渲染后，显示的路由内容，（这个功能，实战中常用）
  {
    path:"/goods", //(写成斜杠加组件名前缀的形式)
    component:Goods //component:导入的组件1
  },
  {
   path:"/Ratings", //(写成斜杠加组件名前缀的形式)
   component:Ratings //component:导入的组件2
  },
  {
    path:"/seller", //(写成斜杠加组件名前缀的形式)
    component:Seller //component:导入的组件2
   }
];
const router = new VueRouter({ //声明路由实例
  linkExactActiveClass: 'active',
  routes,
mode:"history" // 去掉网址上默认的“#”号
});
/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: { App },
  template: '<App/>',
  router
})



