<template>
  <div class="header">
     <!-- 顶部通栏 开始 -->
   <div class="top-wrapper">
			<div class="back-wrapper">
				<span class="icon-arrow_lift"></span>
			</div>

			<form class="search-wrapper">
				<span class="search-icon"></span>
				<input class="search-bar" type="text" placeholder="搜索店内商品" />
			</form>

			<div class="more-wrapper">
				<a class="spelling-bt" href="#">拼单</a>
				<div class="more-bt">
					<i class="s-radius"></i>
					<i class="s-radius"></i>
					<i class="s-radius"></i>
				</div>
			</div>
		</div>
    <!-- 顶部通栏 结束 -->
    

    <!-- 主题内容 开始 -->
    <div class="content-wrapper">
			<div class="icon" :style="head_bg">
				<!--<img :src="poiInfo.pic_url" />-->
        <!--优先选img标签引用样式，注意img标签引用的图片，无法的样式无法起作用时
        ，用computed选项解决 -->
			</div>
			<div class="name">
				<h3>{{poiInfo.name}}</h3>
			</div>
			<div class="collect">
				<img src="./img/star.png" />
				<span>收藏</span>
			</div>
		</div>
    <!-- 主题内容 结束 -->
    

    <!-- 公告内容 开始 -->
    <div class="bulletin-wrapper">
			<div class="icon" v-if="poiInfo.discounts2" :style="shouye_bg" >
      </div>
      <span class="text" v-if="poiInfo.discounts2">{{poiInfo.discounts2[0].info}}</span>
      <div class="detail" v-if="poiInfo.discounts2" @click="showBulletin">
        <span>{{poiInfo.discounts2.length}}个活动</span>
        <span class="icon-keyboard_arrow_right"></span>
      </div>
			</div>
    <!-- 问题:必须加v-if="poiInfo.discounts2"，否则会出现0没有定义 
    Cannot read property '0' of undefined" -->
    <!-- 公告内容 结束 -->
    

    <!-- 背景内容 开始 -->
    <div class="bg-wrapper" :style="head_pic_url">
   <!-- <div class="bg-wrapper" :style='"background:url("+poiInfo.head_pic_url+")"'> --> -->
			<!-- <img :src="poiInfo.head_pic_url" /> -->
      
		 </div>
    
    <!-- 背景内容 结束 -->

    <!-- 公告详情 开始 -->
    <transition name="bulletin-detail1">
      <div class="bulletin-detail" v-show="isShow">
          <div class="detail-wrapper">
            <!-- 相关内容容器 -->
            <div class="main-wrapper" :style="detail_bg">
              <div class="icon" :style="head_bg"></div>
              <h3 class="name">{{poiInfo.name}}</h3>
              <!-- 星级评价 -->
              <div class="score">
                <app-star :score="poiInfo.wm_poi_score"></app-star>
                <span>{{poiInfo.wm_poi_score}}</span>
              </div>

              <p class="tip">
                {{poiInfo.min_price_tip}} <i>|</i> {{poiInfo.shipping_fee_tip}} <i>|</i> {{poiInfo.delivery_time_tip}}
                <!-- <i>|</i>表示加一个倾斜的斜杠 -->
              </p>

              <p class="time">
                配送时间: {{poiInfo.shipping_time}}
              </p>

              <div class="discounts" v-if="poiInfo.discounts2">
                <p>
                  <img :src="poiInfo.discounts2[0].icon_url" />
                  <span>{{poiInfo.discounts2[0].info}}</span>
                </p>
              </div>
            </div>

            <!-- 关闭内容容器 -->
            <div class="close-wrapper">
              <span class="icon-close" @click="closeBulletin"></span>
              <!-- class="icon-close"为span标签加了一个叉号图标，即引用了
              icon.css下的字体图标 -->
            </div>
          </div>
      </div>
    </transition> 
    <!-- 公告详情 结束 -->

  </div>
</template>

<script>
// import HelloWorld from './components/HelloWorld'
import Star from "../star/Star";
export default {
  data() {
    return {
      isShow: false
    };
  },
  props: {
    poiInfo: {
      type: Object,
      default: {}
    }
  },
  computed: {
    head_pic_url() {
      return "background-image: url(" + this.poiInfo.head_pic_url + ");";
    },
    head_bg() {
      return "background-image: url(" + this.poiInfo.pic_url + ");";
    },
    shouye_bg() {
      return (
        "background-image: url(" + this.poiInfo.discounts2[0].icon_url + ");"
      );
    },
    detail_bg() {
      return "background-image: url(" + this.poiInfo.poi_back_pic_url + ");";
    }
  },
  methods: {
    showBulletin() {
      this.isShow = true;
    },
    closeBulletin() {
      this.isShow = false;
    }
  },
  // name: 'header',
  components: {
    "app-star": Star
  }
};
</script>

<style scoped>
@import url(../../common/css/icon.css);
/* 注意调用css时，用link 和 @import url的区别 
我认为：结合使用最好，html文件中用link，组件中用@import*/
.header {
  height: 130px;
  padding-top: 20px;
  /* position: relative; */
  /* border:1px solid red; */
}

/* 顶部通栏样式 */
.header .top-wrapper {
  position: relative;
}

.header .top-wrapper .back-wrapper {
  width: 50px;
  height: 31px;
  position: absolute;
  left: 0;
  top: 0;
  /* border: 1px solid green; */
  text-align: center;
  line-height: 31px;
  /* 字体水平垂直居中 */
}

.header .top-wrapper .back-wrapper span {
  color: white;
}
.header .top-wrapper .search-wrapper {
  width: 100%;
  height: 31px;
  /* 由于form表单(非脱标元素)左右相连的元素都脱标，当设置 width: 100%;和background: pink;时，
背景颜色铺满窗口的整个宽度 */
  /* background: pink; */
  padding: 0 104px 0 50px;
  /* 设置盒子从边框开始计算*/
  box-sizing: border-box;
  /* border:1px solid yellow; */
}

.header .top-wrapper .search-wrapper .search-icon {
  width: 28px;
  height: 31px;
  /* border:1px solid red; */
  background: url(./img/search.png) no-repeat;
  background-position: center center;
  background-size: 15px auto;
  /*规律： 当精灵图只有一个且背景图片正好容纳该精灵图时，一般设
  background-position: center center; 而background-size大小为实际
  图片的大小的一半*/
  position: absolute;
  top: 0;
  left: 50px;
}

.header .top-wrapper .search-wrapper .search-bar {
  width: 100%;
  height: 31px;
  border: 0;
  /* 设置盒子从边框开始计算*/
  box-sizing: border-box;
  /* background: #cdcdcc; */
  border-radius: 25px;
  /* 字体距离输入框右边内边距为28px */
  padding-left: 28px;

  /* 去除选中时蓝色边框*/
  outline: none;
}
.header .top-wrapper .more-wrapper {
  /* border:1px solid black; */
  width: 65px;
  height: 24px;
  /* background: orange; */
  position: absolute;
  right: 0;
  top: 0;
  padding: 7px 15px 0 24px;
  /* 盒子的总宽度：65+左右padding（15+24）=104px */
}
.header .top-wrapper .more-wrapper .spelling-bt {
  width: 30px;
  height: 17px;
  color: white;
  line-height: 17px;
  border: 1px solid white;
  text-align: center;
  float: left;
  text-decoration: none;
  font-size: 10px;
}
.header .top-wrapper .more-wrapper .more-bt {
  float: right;
  /* outline: thin dotted red; */
  width: 20px;
  height: 24px;
  margin-left: 13px;
  margin-top: 6px;
}
.header .top-wrapper .more-wrapper .more-bt .s-radius {
  width: 3px;
  height: 3px;
  border-radius: 50%;
  border: 1px solid white;
  display: block;
  float: left;
  margin-right: 1px;
}
/* 背景图片样式 */
.header .bg-wrapper {
  border: 3px solid yellow;
  width: 100%;
  height: 150px;
  /* border: 1px solid blue; */
  position: absolute;
  left: 0;
  top: 0;
  z-index: -99999;
  background-size: 100% 135%;
  background-position: center -12px;
  /* 规律1（当图片的宽度需要撑满body时）：一般	background-position
    第一个值为center 要想使图片处于竖向拉伸状态，图片的位置上移，且图片的
    高度变大，秉承这个原则，配合调整-12px和135%两个值，已达到理想效果*/
  /* background-repeat: no-repeat; */
}
/* .header .bg-wrapper img{
  width: 100%;
  height:180px;
} 通过改变高度，使图片变形（拉伸或压缩），通过绑定style属性引入背景图片
改变 background-size和background-position在保证图片不变形的情况下改变图片的大小
问题：这两个属性不起作用，用computed解决*/

/* 主题内容样式 */
.header .content-wrapper {
  /* outline: thin dotted white; */
  padding: 17px 10px 11px;
  height: 50px;
  overflow: hidden;
}

.header .content-wrapper .icon {
  /* outline: thin dotted white; */
  width: 50px;
  height: 50px;
  /* outline: thin dotted yellow; */
  background-size: 135% 100%;
  background-position: center;
  border-radius: 5px;
  float: left;

  /* 规律2：（当图片的高度需撑满盒子的高度时）
  一般设 background-position: center;通过扩大background-size的宽度，
  使图片横向拉伸 */
}
.header .content-wrapper .name {
  float: left;
  /* outline: thin dotted white; */
  padding: 18px 0 0 12px;
}
.header .content-wrapper .name h3 {
  font-size: 16px;
  font-weight: bold;
  color: white;
}
.header .content-wrapper .collect {
  width: 25px;
  height: 37px;
  float: right;
  text-align: center;
  padding-top: 6px;
  /* border:1px solid red; */
}
.header .content-wrapper .collect img {
  width: 20px;
  height: 20px;
}
.header .content-wrapper .collect span {
  margin-top: 7px;
  color: white;
  font-size: 11px;
}
/* 公告内容样式 */
.header .bulletin-wrapper {
  /* outline: thin dotted red; */
  height: 16px;
  padding: 0 10px;
  overflow: hidden;
}
.header .bulletin-wrapper .icon {
  width: 16px;
  height: 16px;
  float: left;
  margin-right: 6px;
  /* outline: thin dotted green; */
  background-position: center;
  background-size: 15px auto;
  /* background-repeat: no-repeat; */
  /* 注意当引用json数据内的图片时，要有computed选项，为图片设置属性值，否则出问题 */
}
.header .bulletin-wrapper .text {
  font-size: 11px;
  color: white;
  float: left;
  line-height: 16px;
}
.header .bulletin-wrapper .detail {
  color: white;
  float: right;
  font-size: 11px;
  line-height: 16px;
  /* outline: thin dotted white; */
}
.header .bulletin-wrapper .detail span {
  font-size: 16px;
  line-height: 16px;
}
/* 公告详情 样式  */
.header .bulletin-detail {
  width: 100%;
  height: 100%;
  position: absolute;
  /* 脱标元素的宽和高设为100%是铺满整个窗口，不再参考的div父元素的宽高 */
  left: 0;
  top: 0;
  background: rgba(98, 98, 98, 0.8);
  /* 若要显示灰色背景下的内容，就要对灰色背景设置透明度 
    延伸：若要隐藏灰色背景下的内容，就要增大权值*/
  z-index: 999;
  /* 增大权值，让bulletin-detail盒子压盖在其他元素的最上方， */
  /* border:1px solid green; */
}

.header .bulletin-detail .detail-wrapper {
  width: 100%;
  height: 100%;
  padding: 43px 20px 125px;
  box-sizing: border-box;
}

.header .bulletin-detail .detail-wrapper .main-wrapper {
  width: 100%;
  height: 100%;
  background-size: 100% 100%;
  border-radius: 10px;
  text-align: center;
}

.header .bulletin-detail .detail-wrapper .main-wrapper .icon {
  width: 60px;
  height: 60px;
  background-size: 135% 100%;
  background-position: center;
  border-radius: 5px;
  display: inline-block;
  margin-top: 40px;
}

.header .bulletin-detail .detail-wrapper .main-wrapper .name {
  font-size: 15px;
  color: white;
  margin-top: 13px;
}

.header .bulletin-detail .detail-wrapper .main-wrapper .score {
  height: 10px;
  margin-top: 6px;
  text-align: center;
  font-size: 0;
}

.header .bulletin-detail .detail-wrapper .main-wrapper .score .star {
  display: inline-block;
  margin-right: 7px;
}

.header .bulletin-detail .detail-wrapper .main-wrapper .score span {
  display: inline-block;
  font-size: 10px;
  color: white;
}

.header .bulletin-detail .detail-wrapper .main-wrapper .tip {
  font-size: 11px;
  color: #bababc;
  margin-top: 8px;
}

.header .bulletin-detail .detail-wrapper .main-wrapper .tip i {
  margin: 0 7px;
}

.header .bulletin-detail .detail-wrapper .main-wrapper .time {
  font-size: 11px;
  color: #bababc;
  margin-top: 13px;
}

.header .bulletin-detail .detail-wrapper .main-wrapper .discounts {
  margin-top: 20px;
  padding: 0 20px;
  /* border:1px solid red; */
}

.header .bulletin-detail .detail-wrapper .main-wrapper .discounts p {
  padding-top: 20px;
  border-top: 1px solid #bababc;
  /* border:1px solid green; */
}

.header .bulletin-detail .detail-wrapper .main-wrapper .discounts img {
  width: 16px;
  height: 16px;
  /* border:1px solid white; */
  vertical-align: middle;
}

.header .bulletin-detail .detail-wrapper .main-wrapper .discounts span {
  font-size: 11px;
  line-height: 16px;
  color: white;
  /* border:1px solid green; */
  vertical-align: middle;
}

.header .bulletin-detail .detail-wrapper .close-wrapper {
  padding-top: 20px;
  height: 40px;
  text-align: center;
}

.header .bulletin-detail .detail-wrapper .close-wrapper span {
  width: 40px;
  height: 40px;
  line-height: 40px;
  border-radius: 50%;
  font-size: 14px;
  color: white;
  display: inline-block;
  background: rgba(118, 118, 118, 0.7);
  border: 1px solid rgba(140, 140, 140, 0.9);
}

/* 动画效果 */
.bulletin-detail1-enter-active,
.bulletin-detail1-leave-active {
  transition: 2s all;
}

.bulletin-detail1-enter,
.bulletin-detail1-leave-to {
  opacity: 0;
}

.bulletin-detail1-enter-to,
.bulletin-detail1-leave {
  opacity: 1;
}
</style>



