<template>
  <div class="star">
    <span class="star-item" v-for="(itemClass,index) in itemClasses" :key="index" :class="itemClass">
    </span>
    <!--:class="itemClass"表示对class属性绑定对应的值，分别为on、half、off -->
  </div>
</template>

<script>
// 星星长度
const LENGTH = 5;

// 星星的状态
const CLS_ON = "on";
const CLS_HALF = "half";
const CLS_OFF = "off";

export default {
  name: "Star",
  // props:{
  //   score:{
  //     type:Number
  //   }
  // },
  props: ["score"],
  computed: {
    itemClasses() {
      let result = [];

      // 4.7 --->4.5
      let score = parseInt(this.score * 2) / 2;

      // 一个半星 4.5/1!==0
      let hasDecimal = score % 1 !== 0;

      // 4个全星
      let integer = parseInt(score);
      /* 4.7表示4个全星，1个半星  3.7表示3个全星，1个半星，一个空星 */
      // 遍历全星
      for (let i = 0; i < integer; i++) {
        result.push(CLS_ON);
      }

      // 处理半星
      if (hasDecimal) {
        result.push(CLS_HALF);
      }

      // 补齐
      while (result.length < LENGTH) {
        result.push(CLS_OFF);
      }

      return result;
    }
  }
};
</script>

<style>
.star {
  font-size: 0;
}
.star .star-item {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin-right: 3px;
  background-repeat: no-repeat;
  background-size: 10px 10px;
  /* outline: thin dotted red; */
}
.star .star-item:last-child {
  margin-right: 0;
}

/* 三种图片类型*/
.star .on {
  background-image: url(img/star24_on@2x.png);
}
.star .half {
  background-image: url(img/star24_half@2x.png);
}
.star .off {
  background-image: url(img/star24_off@2x.png);
}
</style>




